*{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body{
    padding: 30px;
}

input {
    color: rgb(160, 160, 168);
    font-size: 1.3rem;
    width: 100%;
}

.card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img{
    max-height: 170px;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}